import React from "react";
import Project from "./Project";
import { Container } from "@mui/material";
import Image1 from "../images/weatherApp.jpg";

function Projects() {
  return (
    <Container id="projects" sx={{ pt: "50px" }}>
      <h2>Moje projekty</h2>
      <p>
        Poniej przedstawiam kilka wikszych projektw, ktre wykonaem w celu
        zwikszania swoich umiejtnoci.
      </p>
      <Project
        img={Image1}
        title="Aplikacja pogodowa"
        description="Tematem tego projektu jest aplikacja pogodowa. Jej zadaniem jest wywietlenie kilku podstawowych informacji o pogodzie, godzinie itd., po okreleniu nazwy miasta. Aktualnie wykorzystano wiedz z express.js/node.js, komponentw React and MUI, wykorzystania API czasu rzeczywistego, stylw CSS, konstruowaniu responsywnych stron oraz komunikacji midzy front- i backendem."
        more="https://github.com/WojciechZ04/weather-app"
      />
      <Project
        img="https://static.vecteezy.com/system/resources/previews/000/552/199/original/gears-vector-icon.jpg"
        title="Keeper"
        description="Aplikacja 'Keeper' zostaa zainspirowana aplikacj Google Keep. Suy do szybkiego tworzenia notatek, w ktrych mona zapisa swoje pomysy. Projekt skupia si w caoci na wykorzystywanym frameworku Reacta."
        more="https://github.com/WojciechZ04/keeper-app4"
      />
      <Project
        img="https://static.vecteezy.com/system/resources/previews/000/552/199/original/gears-vector-icon.jpg"
        title="To Do List"
        description="Prosta interaktywna aplikacja listy do zrobienia. Jak przystao na list rzeczy do zrobienia, aplikacja ma moliwo dodawania wasnych zada do listy oraz usuwanie tych, ktre zostay ukoczone. Istnieje rwnie moliwo tworzenia wasnych list poprzez rozszerzenie adresu url o nazw listy (np. dodanie /praca, utworzy list z tytuem 'praca'). Projekt wykorzystuje baz danych shostowan za pomoc MongoDB Atlas. Do utworzenia aplikacji wykorzystano jzyk ejs."
        more="https://github.com/WojciechZ04/to_do_list"
      />
      <p className="text">
        Wicej projektw znajdziesz{" "}
        <a href="https://github.com/WojciechZ04">tutaj</a>.
      </p>
    </Container>
  );
}

export default Projects;
